<template>
   <div>
      <div class="title-section">
         <h2>الوثائق المرسلة</h2>
      </div>

      <div v-for="(section, index) in studentGraduations" :key="index">
         <div class="masters-section">
            <div class="master-card">
               <v-row no-gutters align="center">
                  <v-col cols="1">{{ index + 1 }}</v-col>
                  <v-col cols="2">{{ section.studentName }}</v-col>
                  <v-col cols="3"> محل وتاريخ الولادة: {{ section.dob }}</v-col>
                  <v-col cols="2"> المعدل : {{ section.average }}</v-col>
                  <v-col cols="1">التقدير : {{ section.averageWriting }}</v-col>
                  <v-col cols="2" style="text-align:center">
                     سنة القبول : {{ section.enterYear }}
                  </v-col>
                  <v-col cols="1" style="text-align:center">
                     التسلسل : {{ section.sequence }}
                  </v-col>
               </v-row>
            </div>
         </div>
      </div>

      <v-snackbar
         v-model="snackbar"
         :multi-line="multiLine"
         shaped
         color="error"
      >
         {{ message }}

         <template v-slot:action="{ attrs }">
            <v-btn
               color="background"
               text
               v-bind="attrs"
               @click="snackbar = false"
            >
               اغلاق
            </v-btn>
         </template>
      </v-snackbar>
   </div>
</template>

<script>
export default {
   data() {
      return {
         studentGraduations: [],
         message: "",
         snackbar: false,
         multiLine: true,
      };
   },
   created() {
      this.searchForStudent();
   },
   methods: {
      searchForStudent() {
         let self = this;
         let phaseDate = JSON.parse(localStorage.getItem("user")).perv;
         console.log(phaseDate);

         this.$http
            .get(`${this.$store.state.apiUrl}/graduationSection/${phaseDate}`)
            .then((res) => {
               self.studentGraduations = res.data;
               self.storeLoading = false;
            })
            .catch((e) => {
               if (e.response.status == 404) {
                  self.message = "لا يوجد طلبة في هذا القسم";
                  self.snackbar = true;
                  self.storeLoading = false;
               }
            });
      },
   },
};
</script>

