<template>
   <div>
      <div class="title-section">
         <h2>المواد الدراسية</h2>
      </div>
      <v-row>
         <v-col class="d-flex" cols="12" sm="6">
            <v-select
               v-model="masterSelected"
               :items="allMasters"
               item-text="masterDetails"
               item-value="masterId"
               @change="selectMaster"
               label="الشيتات"
               dense
               clearable
               outlined
            ></v-select>
         </v-col>
      </v-row>

      <div v-for="(lesson, index) in masterLessons" :key="index">
         <div class="masters-section">
            <div class="master-card">
               <v-row no-gutters align="center">
                  <v-col cols="3">{{ index + 1 }}</v-col>
                  <v-col cols="3">{{ lesson.lessonName }}</v-col>
                  <v-col cols="3">{{ lesson.enName }}</v-col>

                  <v-col cols="2" style="text-align:left">
                     <v-btn
                        class="mx-2"
                        fab
                        dark
                        small
                        depressed
                        color="primary"
                        @click="editLesson(lesson)"
                     >
                        <v-icon dark size="20">
                           fa-edit
                        </v-icon>
                     </v-btn>
                  </v-col>
               </v-row>
            </div>
         </div>
      </div>

      <v-dialog v-model="dialog2" persistent max-width="400">
         <v-card>
            <v-card-title
               class="headline"
               style="text-align:center;padding:0;margin-bottom:10px;background:#145374"
            >
               <p style="text-align:center;margin: 10px auto;color:#fff">
                  تعديل المادة
               </p>
            </v-card-title>

            <v-card-text>
               <!-- <p style="text-align:center">
                  عند الموافقة على اعتماد الشيت فأنه لايمكن حذفه او التعديل عليه
                  ، هل انت متاكد من الموافقة ؟
               </p> -->

               <div style="height:10px"></div>
               <v-text-field
                  v-model="lesson.lessonName"
                  label="اسم المادة بالعربي"
                  outlined
                  dense
               ></v-text-field>
               <v-text-field
                  v-model="lesson.enName"
                  label="اسم المادة بالانكليزي"
                  outlined
                  dense
               ></v-text-field>
            </v-card-text>
            <v-card-actions>
               <v-spacer></v-spacer>
               <v-btn color="green darken-1" text @click="updateLesson">
                  موافق
               </v-btn>
               <v-btn color="red darken-1" text @click="dialog2 = false">
                  الغاء
               </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-snackbar
         v-model="snackbar"
         :multi-line="multiLine"
         shaped
         :color="snakeColor"
      >
         {{ message }}

         <template v-slot:action="{ attrs }">
            <v-btn
               color="background"
               text
               v-bind="attrs"
               @click="snackbar = false"
            >
               اغلاق
            </v-btn>
         </template>
      </v-snackbar>
   </div>
</template>

<script>
export default {
   data: () => ({
      allMasters: [],
      masterSelected: "",
      lesson: "",
      snackbar: false,
      multiLine: true,
      message: "",
      snakeColor: "",
      masterLessons: [],
      dialog2: false,
      showLessons: false,
   }),

   created() {
      let self = this;
      self.$http
         .get(
            `${self.$store.state.apiUrl}/masters/${self.$store.state.user.perv}`
         )
         .then((res) => {
            let mastersDetails = res.data.map((master) => {
               return {
                  masterId: master.idMaster,
                  masterDetails: `${self.getLevel(master.level)} - ${
                     master.year
                  } - ${master.typeName} - ${master.studyType}`,
               };
            });
            console.log(mastersDetails);
            self.allMasters = mastersDetails;
         })
         .catch((e) => {
            console.log(e);
         });
   },
   methods: {
      selectMaster() {
         let self = this;
         self.$http
            .get(`${self.$store.state.apiUrl}/lessons/${this.masterSelected}`)
            .then((res) => {
               this.masterLessons = res.data;
            })
            .catch((e) => {
               console.log(e);
            });
      },
      editLesson(lesson) {
         this.lesson = lesson;
         this.dialog2 = true;
      },
      updateLesson() {
         let self = this;
         self.$http
            .put(
               `${self.$store.state.apiUrl}/lesson/${self.lesson.idLesson}`,
               self.lesson
            )
            .then((res) => {
               console.log(res);
               self.message = "تم تعديل البيانات";
               self.snackbar = true;
               self.snakeColor = "success";
               self.selectMaster();
               self.dialog2 = false;
            })
            .catch((e) => {
               console.log(e);
               self.message = "حدث خطأ في تعديل البيانات";
               self.snackbar = true;
               self.snakeColor = "error";
               self.dialog2 = false;
            });
      },
      getLevel(level) {
         switch (level) {
            case 1:
               return "المرحلة الاولى";
            case 2:
               return "المرحلة الثانية";
            case 3:
               return "المرحلة الثالثة";
            case 4:
               return "المرحلة الرابعة";
            case 5:
               return "المرحلة الخامسة";
         }
      },
   },
};
</script>

