<template>
   <div class="home">
      <v-container :fluid="false">
         <v-row align-content="center">
            <v-col style="text-align:center">
               <img src="./../assets/logo.png" width="600px" />
            </v-col>
         </v-row>

         <v-row align-content="center" v-if="$store.state.user.perv != -1">
            <v-col style="text-align:center" cols="2">
               <span>اختيار الطلبة</span>

               <v-autocomplete
                  v-model="chosenSearch"
                  :items="searchTerm"
                  item-text="name"
                  item-value="term"
                  color="blue"
                  @change="changeSearchTerm"
                  outlined
                  dense
                  label="الطلبة"
               >
               </v-autocomplete>
            </v-col>
         </v-row>

         <v-row align-content="center" v-if="!showRegister">
            <v-col style="text-align:center;width:300px">
               <canvas id="bar-chart"></canvas>
            </v-col>
         </v-row>

         <v-row align-content="center" v-if="showRegister">
            <v-col style="text-align:center;width:300px">
               <h1>لوحة التحكم لقسم التسجيل</h1>
            </v-col>
         </v-row>
      </v-container>
   </div>
</template>

<script>
// @ is an alias to /src
const Chart = require("chart.js");
export default {
   name: "Home",
   data: () => ({
      students: [],
      showRegister: false,
      chosenSearch: "",
      searchTerm: [
         { levelId: 1, name: "المستمرين", term: "status=1" },
         { levelId: 2, name: "خريجوا ٢٠١٩ - ٢٠٢٠", term: "grdat=2020-2019" },
      ],
   }),
   created() {
      let self = this;
      let user = this.$store.state.user;
      console.log(user);
      if (user.perv != -1) {
         if (user.perv == 30) {
            this.$http
               .get(
                  `${this.$store.state.oldUrl}/levels?sectionId=${this.$store.state.user.perv}&level=5&year=${this.$store.state.user.currentYear}`
               )
               .then((res) => {
                  console.log(res.data);
                  if (res.data.length == 0) {
                     this.$store.state.levelType = 1;
                  } else {
                     this.$store.state.levelType = res.data[0].type * 1;
                  }
               })
               .catch((e) => {
                  console.log(e);
               });

            this.$http
               .get(
                  `${this.$store.state.oldUrl}/studentInfo?sectionId=${this.$store.state.user.perv}&level=5&status=1&page=1&limit=1000`
               )
               .then((res) => {
                  this.students = res.data;
                  console.log(this.students);
                  let female = (function() {
                     let data = res.data.filter((student) => {
                        return student.sex == "انثى";
                     });

                     return data.length;
                  })();

                  let male = (function() {
                     let data = res.data.filter((student) => {
                        return student.sex == "ذكر";
                     });

                     return data.length;
                  })();

                  let firstClass = (function() {
                     let data = res.data.filter((student) => {
                        return student.class == "A";
                     });

                     return data.length;
                  })();

                  let secondClass = (function() {
                     let data = res.data.filter((student) => {
                        return student.class == "B";
                     });

                     return data.length;
                  })();

                  let thirdClass = (function() {
                     let data = res.data.filter((student) => {
                        return student.type == "صباحي";
                     });

                     return data.length;
                  })();

                  let fourthClass = (function() {
                     let data = res.data.filter((student) => {
                        return student.type == "مسائي";
                     });

                     return data.length;
                  })();

                  this.barChart(
                     document.getElementById("bar-chart"),
                     [
                        "اناث",
                        "ذكور",
                        "مجموع الطلبة",
                        "شعبة A",
                        "شعبة B",
                        "صباحي",
                        "مسائي",
                     ],
                     [
                        female,
                        male,
                        res.data.length,
                        firstClass,
                        secondClass,
                        thirdClass,
                        fourthClass,
                     ]
                  );
               });
         } else {
            this.$http
               .get(
                  `${this.$store.state.oldUrl}/levels?sectionId=${this.$store.state.user.perv}&level=4&year=${this.$store.state.user.currentYear}`
               )
               .then((res) => {
                  console.log(res.data);
                  if (res.data.length == 0) {
                     this.$store.state.levelType = 1;
                  } else {
                     this.$store.state.levelType = res.data[0].type * 1;
                  }
               })
               .catch((e) => {
                  console.log(e);
               });

            this.$http
               .get(
                  `${this.$store.state.oldUrl}/studentInfo?sectionId=${this.$store.state.user.perv}&level=4&status=1&page=1&limit=1000`
               )
               .then((res) => {
                  this.students = res.data;
                  let female = (function() {
                     let data = res.data.filter((student) => {
                        return student.sex == "انثى";
                     });

                     return data.length;
                  })();

                  let male = (function() {
                     let data = res.data.filter((student) => {
                        return student.sex == "ذكر";
                     });

                     return data.length;
                  })();

                  let firstClass = (function() {
                     let data = res.data.filter((student) => {
                        return student.class == "A";
                     });

                     return data.length;
                  })();

                  let secondClass = (function() {
                     let data = res.data.filter((student) => {
                        return student.class == "B";
                     });

                     return data.length;
                  })();

                  let thirdClass = (function() {
                     let data = res.data.filter((student) => {
                        return student.type == "صباحي";
                     });

                     return data.length;
                  })();

                  let fourthClass = (function() {
                     let data = res.data.filter((student) => {
                        return student.type == "مسائي";
                     });

                     return data.length;
                  })();

                  this.barChart(
                     document.getElementById("bar-chart"),
                     [
                        "اناث",
                        "ذكور",
                        "مجموع الطلبة",
                        "شعبة A",
                        "شعبة B",
                        "صباحي",
                        "مسائي",
                     ],
                     [
                        female,
                        male,
                        res.data.length,
                        firstClass,
                        secondClass,
                        thirdClass,
                        fourthClass,
                     ]
                  );
               });
         }
      } else {
         this.showRegister = true;
      }
   },
   methods: {
      changeSearchTerm() {
         this.$store
            .dispatch("setSearchTermData", this.chosenSearch)
            .then(() => {
               console.log(this.$store.state.searchTerm);
            });
      },
      barChart(canvasId, labels, data) {
         canvasId.style.width = 300;
         new Chart(canvasId, {
            type: "bar",
            data: {
               labels: labels,
               datasets: [
                  {
                     label: "طالب",
                     backgroundColor: [
                        "#449DD1",
                        "#396581",
                        "#96BC0C",
                        "#0F3747",
                        "#B8D63A",
                        "#22C6FF",
                        "#96BC09",
                        "#c45850",
                     ],
                     data: data,
                  },
               ],
            },
            options: {
               legend: { display: false },
               title: {
                  display: true,
                  text: "احصائيات الطلبة",
               },
            },
         });
      },
   },
};
</script>
<style lang="scss" scoped>
.title-section {
   text-align: center;
   width: 300px;
   margin: 20px auto;
}
.title-section h2 {
   padding: 10px;
   border-radius: 10px;
   background: #ffffff;
   box-shadow: 5px 5px 10px #e0e0e0, -5px -5px 10px #e0e0e0;
}
#bar-chart {
   width: 100% !important;
   height: 600px !important;
   display: inline-block;
}
</style>
