<template>
   <div :key="keyComponent">
      <div class="title-section">
         <h2>توليد الوثائق</h2>
      </div>

      <v-dialog v-model="dialog3" max-width="500px">
         <template v-slot:default="dialog">
            <v-card>
               <v-toolbar color="accent">
                  <h3 style="color:#fff;text-align:center">نوع الوثيقة</h3>
               </v-toolbar>
               <v-card-text>
                  <v-row style="margin:10px">
                     <v-col cols="6" style="text-align:center">
                        <v-btn color="accent" @click="exportDocumentInArabic">
                           <v-icon left>
                              fa-file-word
                           </v-icon>
                           باللغة العربية
                        </v-btn>
                     </v-col>
                     <v-col cols="6" style="text-align:center">
                        <v-btn color="accent" @click="exportDocumentInEnglish">
                           <v-icon left>
                              fa-file-word
                           </v-icon>
                           باللغة الانكليزية
                        </v-btn>
                     </v-col>
                  </v-row>
               </v-card-text>
               <v-card-actions class="justify-end">
                  <v-btn text @click="dialog.value = false">اغلاق</v-btn>
               </v-card-actions>
            </v-card>
         </template>
      </v-dialog>

      <v-dialog v-model="dialogDelete" persistent max-width="290">
         <template v-slot:activator="{ on, attrs }"> </template>
         <v-card>
            <v-card-title
               class="headline"
               style="background:#e23;margin-bottom:10px;color:#ffffff"
            >
               <h6>
                  حذف وثيقة
               </h6>
            </v-card-title>
            <v-card-text>
               هل انت متأكد من حذف هذه الوثيقة ؟
            </v-card-text>
            <v-card-actions>
               <v-spacer></v-spacer>
               <v-btn color="green darken-1" text @click="confirmDelete()">
                  موافق
               </v-btn>
               <v-btn color="red darken-1" text @click="dialogDelete = false">
                  اغلاق
               </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>

      <v-snackbar
         v-model="snackbarSuccess"
         :multi-line="multiLine"
         shaped
         color="success"
      >
         {{ message }}

         <template v-slot:action="{ attrs }">
            <v-btn
               color="background"
               text
               v-bind="attrs"
               @click="snackbar = false"
            >
               اغلاق
            </v-btn>
         </template>
      </v-snackbar>

      <v-snackbar
         v-model="snackbar"
         :multi-line="multiLine"
         shaped
         color="error"
      >
         {{ message }}

         <template v-slot:action="{ attrs }">
            <v-btn
               color="background"
               text
               v-bind="attrs"
               @click="snackbar = false"
            >
               اغلاق
            </v-btn>
         </template>
      </v-snackbar>

      <v-snackbar
         v-model="snackbarSuccess"
         :multi-line="multiLine"
         shaped
         color="green"
      >
         {{ message }}

         <template v-slot:action="{ attrs }">
            <v-btn
               color="background"
               text
               v-bind="attrs"
               @click="snackbar = false"
            >
               اغلاق
            </v-btn>
         </template>
      </v-snackbar>

      <div class="sheet-wrapper">
         <v-stepper v-model="e1" @change="stepperChange" vertical>
            <v-stepper-header>
               <v-stepper-step color="accent" :complete="e1 > 1" step="1">
                  اختر القسم
               </v-stepper-step>
            </v-stepper-header>

            <v-stepper-items>
               <v-stepper-content step="1">
                  <v-row no-gutters>
                     <v-col cols="3">
                        <v-autocomplete
                           v-model="chosenSection"
                           :items="allSections"
                           item-text="name"
                           item-value="id"
                           outlined
                           dense
                           label="القسم"
                        >
                        </v-autocomplete>
                     </v-col>
                  </v-row>
                  <v-btn
                     color="accent"
                     @click="searchForStudent"
                     :loading="storeLoading"
                  >
                     بحث
                  </v-btn>
               </v-stepper-content>
            </v-stepper-items>
         </v-stepper>
      </div>

      <div class="title-section">
         <h2>الوثائق</h2>
      </div>

      <div style="text-align:center">
         <v-row>
            <v-col cols="3" style="margin-right: 50px;">
               <v-autocomplete
                  v-model="foundStudent"
                  :items="sections"
                  @click:clear="clearStudents"
                  item-text="studentName"
                  item-value="studentName"
                  @change="findStudent"
                  outlined
                  dense
                  label="اسم الطالب"
               >
               </v-autocomplete>
            </v-col>
         </v-row>
      </div>

      <div v-for="(section, index) in filteredStudent" :key="index">
         <div class="masters-section">
            <div class="master-card">
               <v-row no-gutters align="center">
                  <v-col cols="1">{{ index + 1 }}</v-col>
                  <v-col cols="2">{{ section.studentName }}</v-col>
                  <v-col cols="2"> محل وتاريخ الولادة: {{ section.dob }}</v-col>
                  <v-col cols="1"> المعدل : {{ section.average }}</v-col>
                  <v-col cols="1">التقدير : {{ section.averageWriting }}</v-col>
                  <v-col cols="2" style="text-align:center"
                     >سنة القبول : {{ section.enterYear }}</v-col
                  >
                  <v-col cols="2" style="text-align:center"
                     >التسلسل : {{ section.sequence }}</v-col
                  >

                  <v-col cols="1" style="text-align:left">
                     <v-btn
                        :loading="exportLoading"
                        class="mx-1"
                        fab
                        dark
                        small
                        depressed
                        color="accent"
                        @click="studentInformation(section)"
                     >
                        <v-icon dark size="20">
                           fa-file-word
                        </v-icon>
                     </v-btn>
                     <v-btn
                        :loading="exportLoading"
                        class="mx-1"
                        fab
                        dark
                        small
                        depressed
                        color="error"
                        @click="deleteDocument(section)"
                     >
                        <v-icon dark size="20">
                           fa-trash
                        </v-icon>
                     </v-btn>
                  </v-col>
               </v-row>
            </div>
         </div>
      </div>
   </div>
</template>

<script>
const path = window.require("path");
import * as fs from "fs";
import Docxtemplater from "docxtemplater";
import PizZip from "pizzip";
import { saveAs } from "file-saver";

export default {
   data() {
      return {
         chosenSection: "",
         show: false,
         storeLoading: false,
         dialogDelete: false,
         snackbarSuccess: false,
         snackbar: false,
         multiLine: true,
         imagePath: "",
         keyComponent: 0,
         message: "",
         allSections: [],
         snackbarSuccess: false,
         sections: [],
         foundStudent: "",
         fourthClass: 0,
         filteredStudent: [],
         documentForDelete: 0,
         lessonTotal: 0,
         e1: 1,
         dialog3: false,
         exportLoading: false,
         phaseData: {},
         studentSelected: {},
         studentAvailableInformation: {},
         studentFirstYear: [],
         studentSecondYear: [],
         studentThirdYear: [],
         fifthLessons: [],
         studentSectionId: "",
         studentFifthYear: [],
         studentFourthYear: [],
         fourthLessons: [],
         fourthType: "",
      };
   },
   created() {
      this.getStudentInfo();
   },
   methods: {
      stepperChange() {
         console.log("stepper");
      },
      setPhaseAndDate(phase) {
         if (phase == 1) {
            return {
               phaseText: "الدور الاول",
               date: this.phaseData.phaseOne,
               phaseTextEn: "FistAttempt",
               dateEn: this.phaseData.phaseOneEn,
            };
         } else {
            return {
               phaseText: "الدور الثاني",
               date: this.phaseData.phaseTwo,
               phaseTextEn: "Second Attempt",
               dateEn: this.phaseData.phaseTwoEn,
            };
         }
      },
      getStudentInfo() {
         let oldUrl = this.$store.state.oldUrl;
         let phaseDate = localStorage.getItem("phaseDate");
         this.phaseData = JSON.parse(phaseDate);
         console.log(this.phaseData);
         this.$http
            .get(`${oldUrl}/sections`)
            .then((res) => {
               this.allSections = res.data;
               console.log(res.data);
            })
            .catch((e) => {
               console.log(e);
            });
      },
      deleteDocument(doc) {
         this.documentForDelete = doc.idGraduationDocument;
         this.dialogDelete = true;
      },
      confirmDelete() {
         this.$http
            .delete(
               `${this.$store.state.apiUrl}/graduationDocument/${this.documentForDelete}`
            )
            .then((res) => {
               this.dialogDelete = false;
               this.message = "تم حذف الوثيقة";
               this.snackbarSuccess = true;
               this.searchForStudent();
               console.log(res);
            });
      },
      clearStudents() {
         let self = this;

         self.filteredStudent = self.sections;
         console.log(self.filteredStudent);
         self.keyComponent += 1;
      },
      findStudent() {
         let self = this;

         self.filteredStudent = self.sections;
         let filtered = self.filteredStudent.filter((student) => {
            return student.studentName == self.foundStudent;
         });

         self.filteredStudent = filtered;
         self.keyComponent += 1;
      },
      checkGenderEn(gender) {
         if (gender == "ذكر") {
            return "he";
         } else {
            return "she";
         }
      },
      checkGender(word, gender) {
         if (gender == "ذكر") {
            return word;
         } else {
            if (word == "صورته") {
               return "صورتها";
            }
            if (word == "تخرج") {
               return "تخرجت";
            }
            if (word == "حصل") {
               return "حصلت";
            }
         }
      },
      numberToWord(number) {
         let numbersWriting = [
            "احدى",
            "اثنتان",
            "ثلاث",
            "اربع",
            "خمس",
            "ست",
            "سبع",
            "ثمان",
            "تسع",
            "عشرة",
            "عشرون",
            "ثلاثون",
            "اربعون",
            "خمسون",
            "ستون",
            "سبعون",
            "ثمانون",
            "تسعون",
            "مئة",
         ];
         switch (number) {
            case 10:
               return numbersWriting[9];
            case 20:
               return numbersWriting[10];
            case 30:
               return numbersWriting[11];
            case 40:
               return numbersWriting[12];
            case 50:
               return numbersWriting[13];
            case 60:
               return numbersWriting[14];
            case 70:
               return numbersWriting[15];
            case 80:
               return numbersWriting[16];
            case 90:
               return numbersWriting[17];
            case 100:
               return numbersWriting[18];
         }

         let writingNumber = [];
         let num = number.toString().split("");
         switch (parseInt(num[1])) {
            case 1:
               writingNumber.push(numbersWriting[0]);
               break;
            case 2:
               writingNumber.push(numbersWriting[1]);
               break;
            case 3:
               writingNumber.push(numbersWriting[2]);
               break;
            case 4:
               writingNumber.push(numbersWriting[3]);
               break;
            case 5:
               writingNumber.push(numbersWriting[4]);
               break;
            case 6:
               writingNumber.push(numbersWriting[5]);
               break;
            case 7:
               writingNumber.push(numbersWriting[6]);
               break;
            case 8:
               writingNumber.push(numbersWriting[7]);
               break;
            case 9:
               writingNumber.push(numbersWriting[8]);
               break;
         }

         switch (parseInt(num[0])) {
            case 1:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[9]);
               break;
            case 2:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[10]);
               break;
            case 3:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[11]);
               break;
            case 4:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[12]);
               break;
            case 5:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[13]);
               break;
            case 6:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[14]);
               break;
            case 7:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[15]);
               break;
            case 8:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[16]);
               break;
            case 9:
               writingNumber.push("و");
               writingNumber.push(numbersWriting[17]);
               break;
         }

         if (writingNumber.length > 0) {
            return writingNumber.join(" ");
         }
      },
      inWords(num) {
         let a = [
            "",
            "One ",
            "Two ",
            "Three ",
            "Four ",
            "Five ",
            "Six ",
            "Seven ",
            "Eight ",
            "Nine ",
            "Ten ",
            "Eleven ",
            "Twelve ",
            "Thirteen ",
            "Fourteen ",
            "Fifteen ",
            "Sixteen ",
            "Seventeen ",
            "Eighteen ",
            "Nineteen ",
         ];
         let b = [
            "",
            "",
            "Twenty",
            "Thirty",
            "Forty",
            "Fifty",
            "Sixty",
            "Seventy",
            "Eighty",
            "Ninety",
         ];

         if ((num = num.toString()).length > 9) return "overflow";
         let n = ("000000000" + num)
            .substr(-9)
            .match(/^(\d{2})(\d{2})(\d{2})(\d{1})(\d{2})$/);
         if (!n) return;
         var str = "";
         str +=
            n[1] != 0 ? a[Number(n[1])] || b[n[1][0]] + " " + a[n[1][1]] : "";
         str +=
            n[2] != 0 ? a[Number(n[2])] || b[n[2][0]] + " " + a[n[2][1]] : "";
         str +=
            n[3] != 0 ? a[Number(n[3])] || b[n[3][0]] + " " + a[n[3][1]] : "";
         str +=
            n[4] != 0 ? a[Number(n[4])] || b[n[4][0]] + " " + a[n[4][1]] : "";
         str +=
            n[5] != 0
               ? (str != "" ? "and " : "") +
                 (a[Number(n[5])] || b[n[5][0]] + " " + a[n[5][1]])
               : "";
         return str;
      },
      checkStudy(study) {
         if (study == "صباحي") {
            return { study: "الصباحية", studyEn: "Morning" };
         } else {
            return { study: "المسائية", studyEn: "Evening" };
         }
      },
      searchForStudent() {
         let self = this;

         self.storeLoading = true;

         let baseUrl = this.$store.state.apiUrl;
         let oldUrl = this.$store.state.oldUrl;
         this.$http
            .get(`${baseUrl}/graduationSection/${self.chosenSection}`)
            .then((res) => {
               self.sections = res.data;
               self.filteredStudent = self.sections;
               console.log(res.data);
               self.storeLoading = false;
            })
            .catch((e) => {
               if (e.response.status == 404) {
                  self.message = "لا يوجد طلبة في هذا القسم";
                  self.snackbar = true;
                  self.storeLoading = false;
               }
            });
      },
      toIndiaDigits(number) {
         let id = ["۰", "۱", "۲", "۳", "٤", "٥", "٦", "۷", "۸", "۹"];
         let stringNumber = number.toString();

         // return stringNumber.toLocaleString('ar-SU')
         let arabicNumber = stringNumber.replace(/[0-9]/g, function(w) {
            return id[+w];
         });

         return arabicNumber.replace(".", ",");
      },
      setSequence(sequenceType, sequence, totalStudents) {
         let self = this;
         if (sequenceType == 2) {
            return `وبتسلسل ${self.toIndiaDigits(
               sequence
            )} من مجموع ${self.toIndiaDigits(
               totalStudents
            )} خريجاً <span style="font-weight : bold">للدورين الاول والثاني وللدراستين الصباحية والمسائية</span>`;
         } else {
            return `وبتسلسل ${self.toIndiaDigits(
               sequence
            )} من مجموع ${self.toIndiaDigits(
               totalStudents
            )} خريجاً <span style="font-weight : bold">للدورين الاول والثاني</span>`;
         }
      },
      exportDocumentInEnglish() {
         let self = this;

         let sectionDetails = this.allSections.find(
            (section) => section.id == self.chosenSection
         );

         if (sectionDetails.id == 30) {
            console.log(
               self.getLastYearLessonsMarks(
                  self.studentSelected.collegeId,
                  self.studentFifthYear
               )
            );
            fs.readFile(
               path.join(__static, "englishDocumentDent.docx"),
               "binary",
               (error, content) => {
                  if (error) {
                     throw error;
                  }

                  var zip = new PizZip(content);
                  var doc = new Docxtemplater(zip);

                  doc.setData({
                     studentName: self.studentSelected.studentNameEn,
                     locationAndBirth: self.studentSelected.dob,
                     certificate: `Bachelor ${sectionDetails.registerName}`,
                     department: sectionDetails.name,
                     enterYear: self.studentAvailableInformation.enter_year,
                     graduationDate: self.setPhaseAndDate(
                        self.studentSelected.phase
                     ).dateEn,
                     studyType: self.checkStudy(this.studentSelected.type)
                        .studyEn,
                     average: self.studentSelected.average,
                     write: self.gradeWrite(
                        self.studentSelected.averageWriting
                     ),
                     seq: self.studentSelected.sequence,
                     total: self.studentSelected.totalStudents,
                     firstAverage: self.studentSelected.firstAverage,
                     gender: self.checkGenderEn(
                        self.studentAvailableInformation.sex,
                        "english"
                     ),

                     post: self.studentSelected.postponementYear
                        ? self.studentSelected.postponementYear
                        : "لايوجد",
                     fail: self.studentSelected.failYears
                        ? self.studentSelected.failYears
                        : "لايوجد",
                     exception: self.studentSelected.exception
                        ? self.studentSelected.exception
                        : "لايوجد",
                     delay: self.studentSelected.yearsDelay
                        ? self.studentSelected.yearsDelay
                        : "لايوجد",

                     firstYearDate: self.studentFirstYear[0].year,
                     firstYear: self.studentFirstYear,
                     firstAttempt: self.checkForSecondTerm(
                        self.studentFirstYear
                     ).en,

                     secondYearDate: self.studentSecondYear[0].year,
                     secondYear: self.studentSecondYear,
                     secondAttempt: self.checkForSecondTerm(
                        self.studentSecondYear
                     ).en,

                     thirdYearDate: self.studentThirdYear[0].year,
                     thirdYear: self.studentThirdYear,
                     thirdAttempt: self.checkForSecondTerm(
                        self.studentThirdYear
                     ).en,

                     fourthYearDate: self.studentFourthYear[0].year,
                     fourthYear: self.studentFourthYear,
                     fourthAttempt: self.checkForSecondTerm(
                        self.studentFourthYear
                     ).en,

                     fifthYearDate: "2019 - 2020",
                     fifthYear: self.getLastYearLessonsMarks(
                        self.studentSelected.collegeId,
                        self.studentFifthYear
                     ),
                     fifthAttempt: self.checkForSecondTermLastYear(
                        self.studentFifthYear
                     ).en,
                  });

                  try {
                     // render the document (replace all occurences of {first_name} by John, {last_name} by Doe, ...)
                     doc.render();
                  } catch (error) {
                     // The error thrown here contains additional information when logged with JSON.stringify (it contains a properties object containing all suberrors).
                     function replaceErrors(key, value) {
                        if (value instanceof Error) {
                           return Object.getOwnPropertyNames(value).reduce(
                              function(error, key) {
                                 error[key] = value[key];
                                 return error;
                              },
                              {}
                           );
                        }
                        return value;
                     }
                     console.log(
                        JSON.stringify({ error: error }, replaceErrors)
                     );

                     if (
                        error.properties &&
                        error.properties.errors instanceof Array
                     ) {
                        const errorMessages = error.properties.errors
                           .map(function(error) {
                              return error.properties.explanation;
                           })
                           .join("\n");
                        console.log("errorMessages", errorMessages);
                        // errorMessages is a humanly readable message looking like this :
                        // 'The tag beginning with "foobar" is unopened'
                     }
                     throw error;
                  }
                  var out = doc.getZip().generate({
                     type: "blob",
                     mimeType:
                        "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
                  }); //Output the document using Data-URI
                  saveAs(out, `${self.studentAvailableInformation.name}.docx`);
               }
            );
         } else {
            if (
               self.studentFirstYear[0].masterTypeId == 1 &&
               self.studentSecondYear[0].masterTypeId == 1 &&
               self.studentThirdYear[0].masterTypeId == 1
            ) {
               fs.readFile(
                  path.join(__static, "encccc.docx"),
                  "binary",
                  (error, content) => {
                     if (error) {
                        throw error;
                     }

                     var zip = new PizZip(content);
                     var doc = new Docxtemplater(zip);

                     doc.setData({
                        studentName: self.studentSelected.studentNameEn,
                        locationAndBirth: self.studentSelected.dob,
                        certificate: `Bachelor ${self.studentSelected.sectionName}`,
                        department: self.studentSelected.sectionName,
                        enterYear: self.studentAvailableInformation.enter_year,
                        graduationDate: self.setPhaseAndDate(
                           self.studentSelected.phase
                        ).dateEn,
                        studyType: self.checkStudy(this.studentSelected.type)
                           .studyEn,
                        average: self.studentSelected.average,
                        write: self.gradeWrite(
                           self.studentSelected.averageWriting
                        ),
                        seq: self.studentSelected.sequence,
                        total: self.studentSelected.totalStudents,
                        firstAverage: self.studentSelected.firstAverage,
                        gender: self.checkGenderEn(
                           self.studentAvailableInformation.sex,
                           "english"
                        ),

                        post: self.studentSelected.postponementYear,
                        fail: self.studentSelected.failYears,
                        exception: self.studentSelected.exception,
                        delay: self.studentSelected.yearsDelay,

                        firstYearDate: self.studentFirstYear[0].year,
                        firstYearOne: self.getMasterSplit(self.studentFirstYear)
                           .courseOne,
                        firstYearTwo: self.getMasterSplit(self.studentFirstYear)
                           .courseTwo,
                        firstAttempt: self.checkForSecondTerm(
                           self.studentFirstYear
                        ).en,
                        firstSuccess: self.checkRule(
                           self.studentFirstYear,
                           self.studentAvailableInformation.sex
                        ),

                        secondYearDate: self.studentSecondYear[0].year,
                        secondYearOne: self.getMasterSplit(
                           self.studentSecondYear
                        ).courseOne,
                        secondYearTwo: self.getMasterSplit(
                           self.studentSecondYear
                        ).courseTwo,
                        secondAttempt: self.checkForSecondTerm(
                           self.studentSecondYear
                        ).en,
                        secondSuccess: self.checkRule(
                           self.studentSecondYear,
                           self.studentAvailableInformation.sex
                        ),

                        thirdYearDate: self.studentThirdYear[0].year,
                        thirdYearOne: self.getMasterSplit(self.studentThirdYear)
                           .courseOne,
                        thirdYearTwo: self.getMasterSplit(self.studentThirdYear)
                           .courseTwo,
                        thirdAttempt: self.checkForSecondTerm(
                           self.studentThirdYear
                        ).en,
                        thirdSuccess: self.checkRule(
                           self.studentThirdYear,
                           self.studentAvailableInformation.sex
                        ),
                        fourthYearDate: "2019-2020",
                        fourthYearOne: self.getMasterSplit(
                           self.getLastYearLessonsMarks(
                              self.studentSelected.collegeId,
                              self.studentFourthYear
                           )
                        ).courseOne,
                        fourthYearTwo: self.getMasterSplit(
                           self.getLastYearLessonsMarks(
                              self.studentSelected.collegeId,
                              self.studentFourthYear
                           )
                        ).courseTwo,
                        fourthAttempt: self.checkForSecondTermLastYear(
                           self.studentFourthYear
                        ).en,
                        fourthSuccess: self.checkRule(
                           self.studentFourthYear,
                           self.studentAvailableInformation.sex
                        ),
                     });
                     try {
                        // render the document (replace all occurences of {first_name} by John, {last_name} by Doe, ...)
