      studentInformation(student) {
         let self = this;

         self.exportLoading = true;
         self.studentSelected = student;

         self.studentSectionId = student.sectionId;

         if (student.sectionId == 30) {
            let firstClass = `${
               self.$store.state.apiUrl
            }/studentLevelDegree?sectionId=${
               student.sectionId
            }&level=1&collageNumber=${parseInt(student.collegeId)}`;

            let secondClass = `${
               self.$store.state.apiUrl
            }/studentLevelDegree?sectionId=${
               student.sectionId
            }&level=2&collageNumber=${parseInt(student.collegeId)}`;

            let thirdClass = `${
               self.$store.state.apiUrl
            }/studentLevelDegree?sectionId=${
               student.sectionId
            }&level=3&collageNumber=${parseInt(student.collegeId)}`;

            let fourthClass = `${
               self.$store.state.apiUrl
            }/studentLevelDegree?sectionId=${
               student.sectionId
            }&level=4&collageNumber=${parseInt(student.collegeId)}`;

            let fifthClass = `${
               self.$store.state.oldUrl
            }/studentAllMarks?sectionId=${
               student.sectionId
            }&level=5&collegeNumber=${parseInt(student.collegeId)}`;

            let lessonsForFifth = `${self.$store.state.oldUrl}/sectionLessonsForDocument?sectionId=${student.sectionId}&level=5`;

            let availableStudentInformation = `${self.$store.state.oldUrl}/student/${student.studentId}`;

            //check either it's course or yearly

            let levelFourthType = `${this.$store.state.oldUrl}/sectionLevel?sectionId=${this.$store.state.user.perv}&level=4&year=2019-2020`;

            function requestFirstClass() {
               return self.$http.get(firstClass);
            }

            function requestSecondClass() {
               return self.$http.get(secondClass);
            }

            function requestThirdClass() {
               return self.$http.get(thirdClass);
            }

            function requestFourthClass() {
               return self.$http.get(fourthClass);
            }

            function requestFifthLessons() {
               return self.$http.get(lessonsForFifth);
            }

            function requestAvailableStudentInformation() {
               return self.$http.get(availableStudentInformation);
            }

            function requestFourthType() {
               return self.$http.get(levelFourthType);
            }

            function requestFifthClass() {
               return self.$http.get(fifthClass);
            }

            Promise.allSettled([
               requestFirstClass(),
               requestSecondClass(),
               requestThirdClass(),
               requestFourthClass(),
               requestAvailableStudentInformation(),
               requestFifthLessons(),
               requestFourthType(),
               requestFifthClass(),
            ])
               .then((responses) => {
                  const responseFirstClass = responses[0];
                  const responseSecondClass = responses[1];
                  const responseThirdClass = responses[2];
                  const responseFourthClass = responses[3];
                  const responseAvailableStudentInformation = responses[4];
                  const responseFifthLessons = responses[5];
                  const responseFourthType = responses[6];
                  const responseFifthClass = responses[7];

                  self.studentFirstYear =
                     responseFirstClass.status == "fulfilled"
                        ? responseFirstClass.value.data
                        : [];

                  self.studentSecondYear =
                     responseSecondClass.status == "fulfilled"
                        ? responseSecondClass.value.data
                        : [];

                  self.studentThirdYear =
                     responseThirdClass.status == "fulfilled"
                        ? responseThirdClass.value.data
                        : [];

                  self.studentFourthYear =
                     responseFourthClass.status == "fulfilled"
                        ? responseFourthClass.value.data
                        : [];

                  self.fifthLessons =
                     responseFifthLessons.status == "fulfilled"
                        ? responseFifthLessons.value.data
                        : [];

                  self.studentFifthYear =
                     responseFifthClass.status == "fulfilled"
                        ? responseFifthClass.value.data
                        : [];

                  self.studentAvailableInformation =
                     responseAvailableStudentInformation.status == "fulfilled"
                        ? responseAvailableStudentInformation.value.data
                        : [];

                  self.fourthType =
                     responseFourthType.status == "fulfilled"
                        ? responseFourthType.value.data
                        : [];

                  self.studentFirstYear = self.studentFirstYear.map(
                     (degree) => {
                        return {
                           collageNumber: degree.collageNumber,
                           course: degree.course,
                           enName: degree.enName,
                           idDegree: degree.idDegree,
                           idLesson: degree.idLesson,
                           idMaster: degree.idMaster,
                           lessonId: degree.lessonId,
                           lessonName: degree.lessonName,
                           level: degree.level,
                           mark: degree.mark,
                           masterId: degree.masterId,
                           masterTypeId: degree.masterTypeId,
                           numberWriting: degree.numberWriting,
                           phase: degree.phase,
                           rule: degree.rule,
                           sectionId: degree.sectionId,
                           studentName: degree.studentName,
                           studyType: degree.studyType,
                           units: degree.units,
                           unitsAr: self.toIndiaDigits(degree.units),
                           year: degree.year,
                           markAr: self.toIndiaDigits(degree.mark),
                           phaseCheck: degree.phase == 2 ? true : false,
                           numberWritingEn: self.inWords(degree.mark),
                        };
                     }
                  );

                  self.studentSecondYear = self.studentSecondYear.map(
                     (degree) => {
                        return {
                           collageNumber: degree.collageNumber,
                           course: degree.course,
                           enName: degree.enName,
                           idDegree: degree.idDegree,
                           idLesson: degree.idLesson,
                           idMaster: degree.idMaster,
                           lessonId: degree.lessonId,
                           lessonName: degree.lessonName,
                           level: degree.level,
                           mark: degree.mark,
                           masterId: degree.masterId,
                           masterTypeId: degree.masterTypeId,
                           numberWriting: degree.numberWriting,
                           phase: degree.phase,
                           rule: degree.rule,
                           sectionId: degree.sectionId,
                           studentName: degree.studentName,
                           studyType: degree.studyType,
                           units: degree.units,
                           unitsAr: self.toIndiaDigits(degree.units),
                           year: degree.year,
                           phaseCheck: degree.phase == 2 ? true : false,
                           markAr: self.toIndiaDigits(degree.mark),
                           numberWritingEn: self.inWords(degree.mark),
                        };
                     }
                  );

                  self.studentThirdYear = self.studentThirdYear.map(
                     (degree) => {
                        return {
                           collageNumber: degree.collageNumber,
                           course: degree.course,
                           enName: degree.enName,
                           idDegree: degree.idDegree,
                           idLesson: degree.idLesson,
                           idMaster: degree.idMaster,
                           lessonId: degree.lessonId,
                           lessonName: degree.lessonName,
                           level: degree.level,
                           mark: degree.mark,
                           masterId: degree.masterId,
                           masterTypeId: degree.masterTypeId,
                           numberWriting: degree.numberWriting,
                           phase: degree.phase,
                           rule: degree.rule,
                           sectionId: degree.sectionId,
                           studentName: degree.studentName,
                           studyType: degree.studyType,
                           units: degree.units,
                           unitsAr: self.toIndiaDigits(degree.units),
                           year: degree.year,
                           phaseCheck: degree.phase == 2 ? true : false,
                           markAr: self.toIndiaDigits(degree.mark),
                           numberWritingEn: self.inWords(degree.mark),
                        };
                     }
                  );

                  self.studentFourthYear = self.studentFourthYear.map(
                     (degree) => {
                        return {
                           collageNumber: degree.collageNumber,
                           course: degree.course,
                           enName: degree.enName,
                           idDegree: degree.idDegree,
                           idLesson: degree.idLesson,
                           idMaster: degree.idMaster,
                           lessonId: degree.lessonId,
                           lessonName: degree.lessonName,
                           level: degree.level,
                           mark: degree.mark,
                           masterId: degree.masterId,
                           masterTypeId: degree.masterTypeId,
                           numberWriting: degree.numberWriting,
                           phase: degree.phase,
                           rule: degree.rule,
                           sectionId: degree.sectionId,
                           studentName: degree.studentName,
                           studyType: degree.studyType,
                           units: degree.units,
                           unitsAr: self.toIndiaDigits(degree.units),
                           year: degree.year,
                           phaseCheck: degree.phase == 2 ? true : false,
                           markAr: self.toIndiaDigits(degree.mark),
                           numberWritingEn: self.inWords(degree.mark),
                        };
                     }
                  );

                  self.dialog3 = true;
                  self.exportLoading = false;
               })
               .catch((e) => {
                  console.log(e);
                  self.message = "حدث خطأ في جلب البيانات";
                  self.snackbar = true;
               });
         } else {
            let firstClass = `${
               self.$store.state.apiUrl
            }/studentLevelDegree?sectionId=${
               student.sectionId
            }&level=1&collageNumber=${parseInt(student.collegeId)}`;

            let secondClass = `${
               self.$store.state.apiUrl
            }/studentLevelDegree?sectionId=${
               student.sectionId
            }&level=2&collageNumber=${parseInt(student.collegeId)}`;
            let thirdClass = `${
               self.$store.state.apiUrl
            }/studentLevelDegree?sectionId=${
               student.sectionId
            }&level=3&collageNumber=${parseInt(student.collegeId)}`;

            let fourthClass = `${
               self.$store.state.oldUrl
            }/studentAllMarks?sectionId=${
               student.sectionId
            }&level=4&collegeNumber=${parseInt(student.collegeId)}`;

            let lessonsForFourth = `${self.$store.state.oldUrl}/sectionLessonsForDocument?sectionId=${student.sectionId}&level=4`;

            let availableStudentInformation = `${self.$store.state.oldUrl}/student/${student.studentId}`;

            //check either it's course or yearly
            let levelTypeStudy = `${
               this.$store.state.oldUrl
            }/levels?sectionId=${student.sectionId}&level=${
               student.sectionId != 30 ? 4 : 5
            }&year="2019-2020"`;

            let levelFourthType = `${this.$store.state.oldUrl}/sectionLevel?sectionId=${this.$store.state.user.perv}&level=4&year=2019-2020`;

            function requestFirstClass() {
               return self.$http.get(firstClass);
            }

            function requestSecondClass() {
               return self.$http.get(secondClass);
            }

            function requestThirdClass() {
               return self.$http.get(thirdClass);
            }

            function requestFourthClass() {
               return self.$http.get(fourthClass);
            }

            function requestFourthLessons() {
               return self.$http.get(lessonsForFourth);
            }

            function requestAvailableStudentInformation() {
               return self.$http.get(availableStudentInformation);
            }

            function requestLevelTypeStudy() {
               return self.$http.get(levelTypeStudy);
            }

            function requestFourthType() {
               return self.$http.get(levelFourthType);
            }

            Promise.allSettled([
               requestFirstClass(),
               requestSecondClass(),
               requestThirdClass(),
               requestFourthClass(),
               requestAvailableStudentInformation(),
               requestFourthLessons(),
               requestLevelTypeStudy(),
               requestFourthType(),
            ])
               .then((responses) => {
                  const responseFirstClass = responses[0];
                  const responseSecondClass = responses[1];
                  const responseThirdClass = responses[2];
                  const responseFourthClass = responses[3];
                  const responseAvailableStudentInformation = responses[4];
                  const responseFourthLessons = responses[5];
                  const responseLevelTypeStudy = responses[6];

                  const responseFourthType = responses[7];

                  self.studentFirstYear =
                     responseFirstClass.status == "fulfilled"
                        ? responseFirstClass.value.data
                        : [];

                  self.studentSecondYear =
                     responseSecondClass.status == "fulfilled"
                        ? responseSecondClass.value.data
                        : [];

                  self.studentThirdYear =
                     responseThirdClass.status == "fulfilled"
                        ? responseThirdClass.value.data
                        : [];

                  self.studentFourthYear =
                     responseFourthClass.status == "fulfilled"
                        ? responseFourthClass.value.data
                        : [];

                  self.fourthLessons =
                     responseFourthLessons.status == "fulfilled"
                        ? responseFourthLessons.value.data
                        : [];

                  self.studentAvailableInformation =
                     responseAvailableStudentInformation.status == "fulfilled"
                        ? responseAvailableStudentInformation.value.data
                        : [];

                  self.fourthType =
                     responseFourthType.status == "fulfilled"
                        ? responseFourthType.value.data
                        : [];

                  if (responseLevelTypeStudy.status == "fulfilled") {
                     if (responseLevelTypeStudy.value.data.length == 0) {
                        this.$store.state.levelType = 1;
                     } else {
                        this.$store.state.levelType =
                           responseLevelTypeStudy.value.data[0].type * 1;
                     }
                  } else {
                     this.$store.state.levelType = 1;
                  }

                  self.studentFirstYear = self.studentFirstYear.map(
                     (degree) => {
                        return {
                           collageNumber: degree.collageNumber,
                           course: degree.course,
                           enName: degree.enName,
                           idDegree: degree.idDegree,
                           idLesson: degree.idLesson,
                           idMaster: degree.idMaster,
                           lessonId: degree.lessonId,
                           lessonName: degree.lessonName,
                           level: degree.level,
                           mark: degree.mark,
                           masterId: degree.masterId,
                           masterTypeId: degree.masterTypeId,
                           numberWriting: degree.numberWriting,
                           phase: degree.phase,
                           rule: degree.rule,
                           sectionId: degree.sectionId,
                           studentName: degree.studentName,
                           studyType: degree.studyType,
                           units: degree.units,
                           unitsAr: self.toIndiaDigits(degree.units),
                           year: degree.year,
                           markAr: self.toIndiaDigits(degree.mark),
                           phaseCheck: degree.phase == 2 ? true : false,
                           numberWritingEn: self.inWords(degree.mark),
                        };
                     }
                  );

                  self.studentSecondYear = self.studentSecondYear.map(
                     (degree) => {
                        return {
                           collageNumber: degree.collageNumber,
                           course: degree.course,
                           enName: degree.enName,
                           idDegree: degree.idDegree,
                           idLesson: degree.idLesson,
                           idMaster: degree.idMaster,
                           lessonId: degree.lessonId,
                           lessonName: degree.lessonName,
                           level: degree.level,
                           mark: degree.mark,
                           masterId: degree.masterId,
                           masterTypeId: degree.masterTypeId,
                           numberWriting: degree.numberWriting,
                           phase: degree.phase,
                           rule: degree.rule,
                           sectionId: degree.sectionId,
                           studentName: degree.studentName,
                           studyType: degree.studyType,
                           units: degree.units,
                           unitsAr: self.toIndiaDigits(degree.units),
                           year: degree.year,
                           phaseCheck: degree.phase == 2 ? true : false,
                           markAr: self.toIndiaDigits(degree.mark),
                           numberWritingEn: self.inWords(degree.mark),
                        };
                     }
                  );

                  self.studentThirdYear = self.studentThirdYear.map(
                     (degree) => {
                        return {
                           collageNumber: degree.collageNumber,
                           course: degree.course,
                           enName: degree.enName,
                           idDegree: degree.idDegree,
                           idLesson: degree.idLesson,
                           idMaster: degree.idMaster,
                           lessonId: degree.lessonId,
                           lessonName: degree.lessonName,
                           level: degree.level,
                           mark: degree.mark,
                           masterId: degree.masterId,
                           masterTypeId: degree.masterTypeId,
                           numberWriting: degree.numberWriting,
                           phase: degree.phase,
                           rule: degree.rule,
                           sectionId: degree.sectionId,
                           studentName: degree.studentName,
                           studyType: degree.studyType,
                           units: degree.units,
                           unitsAr: self.toIndiaDigits(degree.units),
                           year: degree.year,
                           phaseCheck: degree.phase == 2 ? true : false,
                           markAr: self.toIndiaDigits(degree.mark),
                           numberWritingEn: self.inWords(degree.mark),
                        };
                     }
                  );
                  self.dialog3 = true;
                  self.exportLoading = false;
               })
               .catch((e) => {
                  console.log(e);
                  self.message = "حدث خطأ في جلب البيانات";
                  self.snackbar = true;
               });
         }
      },
      checkForSecondTerm(studentDegrees) {
         let secondExam = studentDegrees.find((degree) => degree.phase == 2);
         if (secondExam != undefined) {
            return { en: "Second Attempt", ar: "الدور الثاني" };
         } else {
            return { en: "First Attempt", ar: "الدور الاول" };
         }
      },
      checkForSecondTermLastYear(fourthMarks) {
         let secondExam = fourthMarks.find(
            (degree) =>
               degree.markType == "custom2" || degree.markType == "final2"
         );

         if (secondExam != undefined) {
            return { en: "Second Attempt", ar: "الدور الثاني" };
         } else {
            return { en: "First Attempt", ar: "الدور الاول" };
         }
      },
      goToDocument(student) {
         let self = this;
         self.studentSelected = student;
         console.log(student);
         self.studentInformation(student);

         // self.$store.dispatch("setStudentForPrintDoc", student).then(() => {
         //    self.$router.push("/grad");
         // });
      },
      gradeWrite(grade) {
         if (grade == "مقبول") {
            return "Pass";
         } else if (grade == "متوسط") {
            return "Fair";
         } else if (grade == "جيد") {
            return "Good";
         } else if (grade == "جيد جدا") {
            return "Very Good";
         } else {
            return "Excellent";
         }
      },
      notHave() {
         this.message = "الرجاء تزويد قسم تكنولوجيا المعلومات بالقالب ";
         this.snackbar = true;
      },
      getLastYearMarksTwo(lesson, levelType, lessonDetails) {
         let self = this;
         console.log(lesson);

         let firstExam = lesson.find((degree) => degree.markType == "custom");

         let secondExam = lesson.find((degree) => degree.markType == "custom2");

         if (firstExam != undefined) {
            let total = firstExam.degree;

            return {
               mark: total,
               lessonName: lessonDetails.name,
               enName: lessonDetails.enName,
               units: lessonDetails.credit,
               unitsAr: self.toIndiaDigits(lessonDetails.credit),
               markAr: self.toIndiaDigits(total),
               numberWritingEn: self.inWords(total),
               numberWritingAr: self.numberToWord(total),
               course: lessonDetails.course,
            };
         } else if (secondExam != undefined) {
            let total = secondExam.degree;
            return {
               mark: total,
               lessonName: lessonDetails.name,
               enName: lessonDetails.enName,
               units: lessonDetails.credit,
               unitsAr: self.toIndiaDigits(lessonDetails.credit),
               markAr: self.toIndiaDigits(total),
               numberWritingEn: self.inWords(total),
               numberWritingAr: self.numberToWord(total),
               course: lessonDetails.course,
            };
         }

         if (levelType == 1) {
            let secondFinal = lesson.find(
               (degree) => degree.markType == "final2"
            );

            if (secondFinal != undefined) {
               let average = lesson.filter((mark) => {
                  return (
                     mark.markType != "final1" &&
                     mark.markType != "practicalFinal"
                  );
               });

               let allAverage = average.reduce(function(
                  accumulator,
                  currentValue
               ) {
                  return accumulator + currentValue.degree;
               },
               0);

               return {
                  mark: allAverage,
                  lessonName: lessonDetails.name,
                  enName: lessonDetails.enName,
                  units: lessonDetails.credit,
                  unitsAr: self.toIndiaDigits(lessonDetails.credit),
                  markAr: self.toIndiaDigits(allAverage),
                  numberWritingEn: self.inWords(allAverage),
                  numberWritingAr: self.numberToWord(allAverage),
                  course: lessonDetails.course,
               };
            } else {
               let allAverage = lesson.reduce(function(
                  accumulator,
                  currentValue
               ) {
                  return accumulator + currentValue.degree;
               },
               0);

               return {
                  mark: allAverage,
                  lessonName: lessonDetails.name,
                  enName: lessonDetails.enName,
                  units: lessonDetails.credit,
                  unitsAr: self.toIndiaDigits(lessonDetails.credit),
                  markAr: self.toIndiaDigits(allAverage),
                  numberWritingEn: self.inWords(allAverage),
                  numberWritingAr: self.numberToWord(allAverage),
                  course: lessonDetails.course,
               };
            }
         } else if (levelType == 2) {
            let secondFinal = lesson.find(
               (degree) => degree.markType == "final2"
            );

            if (secondFinal != undefined) {
               let average = lesson.filter((mark) => {
                  return (
                     mark.markType != "final1" &&
                     mark.markType != "th2" &&
                     mark.markType != "pr2" &&
                     mark.markType != "practicalFinal"
                  );
               });

               let allAverage = average.reduce(function(
                  accumulator,
                  currentValue
               ) {
                  return accumulator + currentValue.degree;
               },
               0);

               return {
                  mark: allAverage,
                  lessonName: lessonDetails.name,
                  enName: lessonDetails.enName,
                  units: lessonDetails.credit,
                  unitsAr: self.toIndiaDigits(lessonDetails.credit),
                  markAr: self.toIndiaDigits(allAverage),
                  numberWritingEn: self.inWords(allAverage),
                  numberWritingAr: self.numberToWord(allAverage),
                  course: lessonDetails.course,
               };
            } else {
               let average = lesson.filter((mark) => {
                  return mark.markType != "th2" && mark.markType != "pr2";
               });

               let allAverage = average.reduce(function(
                  accumulator,
                  currentValue
               ) {
                  return accumulator + currentValue.degree;
               },
               0);

               return {
                  mark: allAverage,
                  lessonName: lessonDetails.name,
                  enName: lessonDetails.enName,
                  units: lessonDetails.credit,
                  unitsAr: self.toIndiaDigits(lessonDetails.credit),
                  markAr: self.toIndiaDigits(allAverage),
                  numberWritingEn: self.inWords(allAverage),
                  numberWritingAr: self.numberToWord(allAverage),
                  course: lessonDetails.course,
               };
            }
         }
      },

      getLastYearLessonsMarks(collageNumber, arrayOfStudents) {
         let self = this;

         let foundStudent = arrayOfStudents.filter((student) => {
            if (
               student.college_number == collageNumber ||
               "0" + student.college_number == collageNumber
            ) {
               return student;
            }
         });

         if (foundStudent.length > 0) {
            if (self.studentSectionId == 30) {
               let degreeForStudent = [];

               let studentLesson = self.fifthLessons.map((lesson) => {
                  let newDegree = foundStudent.filter((student) => {
                     return student.lessonId == lesson.id;
                  });

                  if (newDegree.length > 0) {
                     let average = self.getLastYearMarksTwo(
                        newDegree,
                        1,
                        lesson
                     );

                     degreeForStudent.push(average);
                  }
               });

               return degreeForStudent;
            } else {
               let degreeForStudent = [];

               let studentLesson = self.fourthLessons.map((lesson) => {
                  let newDegree = foundStudent.filter((student) => {
                     return student.lessonId == lesson.id;
                  });

                  if (newDegree.length > 0) {
                     let average = self.getLastYearMarksTwo(
                        newDegree,
                        self.$store.state.levelType,
                        lesson
                     );

                     degreeForStudent.push(average);
                  }
               });

               return degreeForStudent;
            }
         } else {
            console.log(self.backupMarks);
            if (
               self.backupMarks.find(
                  (mark) => mark.collageNumber == collageNumber
               )
            ) {
               let marks = self.backupMarks.map((mark) => {
                  return {
                     degree: mark.mark,
                     lessonName: mark.lessonName,
                     credit: mark.units,
                     markType: mark.phase == 1 ? "custom" : "custom2",
                  };
               });
               console.log(marks);
               return marks;
            }

            return [];
         }
      },
   },

   filters: {
      studyType: function(value) {
         if (value == 2) {
            return "للدراستين الصباحية والمسائية";
         } else if (value == 1) {
            return "حسب دراسة الطالب";
         }
      },
      phaseRole: function(value) {
         if (value == 1) {
            return "الدور الاول";
         } else if (value == 2) {
            return "الدور الثاني";
         }
      },
      convertDate: function(date) {
         let ChosenDate = new Date(date);
         return ChosenDate.toLocaleDateString("ar-IQ");
      },
      convertDateEN: function(date) {
         let ChosenDate = new Date(date);
         return ChosenDate.toLocaleDateString("en-US");
      },
      genderCheck: function(gender) {
         if (gender == "ذكر") {
            return "ناجح";
         } else {
            return "ناجحة";
         }
      },
   },
};
</script>

